			width:1.25rem;
		}
.nav .active .nav-item-title{
			color: #fd797b;
		}
.nav .active .nav-item-indicator{
			background-color:#fd797b;
		}
.mc-tabbar {
	position: absolute;
	bottom: 0;
	background-color: white;
	height: 2.45rem;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	z-index: 100;
	-webkit-box-shadow: 0px 0px 6px 0px 
		rgba(255, 181, 181, 0.32);
	        box-shadow: 0px 0px 6px 0px 
		rgba(255, 181, 181, 0.32);
}
.mc-tabbar .active span {
			color: #fd797b !important;
		}
.mc-tabbar .left, .mc-tabbar .right {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		width: calc((100% - 4.75rem)/2);
		height: 100%;
	}
.mc-tabbar .left .item, .mc-tabbar .right .item {
			width: 50%;
			height: 100%;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			    -ms-flex-align: center;
			        align-items: center;
			-webkit-box-pack: center;
			    -ms-flex-pack: center;
			        justify-content: center;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
			    -ms-flex-direction: column;
			        flex-direction: column;
		}
.mc-tabbar .left .item .icon img, .mc-tabbar .right .item .icon img {
					width: 1.25rem;
					height: 1.25rem;
					margin-bottom: 4px;
				}
.mc-tabbar .left .item span, .mc-tabbar .right .item span {
				font-size: .6rem;
				color: #6a5651;
				position: absolute;
				bottom: 0;
			}
.mc-tabbar .center {
		width: 4.75rem;
		height: 100%;
	}
.mc-tabbar .center .action {
			top: -1.6rem;
			height: calc(100% + 1.6rem);
			position: relative;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			    -ms-flex-align: center;
			        align-items: center;
			-webkit-box-pack: center;
			    -ms-flex-pack: center;
			        justify-content: center;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
			    -ms-flex-direction: column;
			        flex-direction: column;
		}
.mc-tabbar .center .action img {
				width: 3rem;
				height: 3rem;
				border-radius: 1.5rem;
				border: .3rem white solid;
				background: white;
				margin-bottom: 4px;
				-webkit-box-shadow: 0px 0px 6px 0px 
				rgba(255, 181, 181, 0.52);
				        box-shadow: 0px 0px 6px 0px 
				rgba(255, 181, 181, 0.52);
			}
.mc-tabbar .center .action span {
				font-size: .6rem;
				color: #6a5651;
				position: absolute;
				bottom: 0;
			}
